import Vue from 'vue'
import Vuex from 'vuex'
import SettingsHelper from './settings.js'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        settings: {}
    },
    mutations: {
        updateSettings(state, updatedSettings) {
            state.settings = updatedSettings;
        }
    },
    actions: {
        initializeSettings(context) {
            let settings = SettingsHelper.readFromLocalStorage();
            context.commit('updateSettings', settings);
        },
        updateSettings(context, settings) {
            SettingsHelper.writeToLocalStorage(settings);
            context.commit('updateSettings', settings);
        }
    }
})