const LOCALSTORAGEKEY = "firstmediaVue.settings";

const settingsHelper = {
    isSettingsValid(settings) {
        if (!settings) {
            return false;
        }

        // if (!('FavSets' in settings)) {
        //     return false;
        // }

        if (!(settings.FavSets instanceof Array)) {
            return false;
        }

        for (const set of settings.FavSets) {
            if (!(set.Channels instanceof Array)) {
                return false;
            }

            for (const channel of set.Channels) {
                if (!('Number' in channel)) {
                    return false;
                }

                if (!('Name' in channel)) {
                    return false;
                }
            }

            if (!('Description' in set)) {
                return false;
            }
        }

        if (!('SelectedSet' in settings)) {
            return false;
        }

        return true;
    },
    getDefaultSettings() {
        let settings = {
            FavSets: [
                {
                    Description: 'First Set',
                    Channels: [
                        { Number: 1, Name: 'BERITA SATU HD' },
                        { Number: 15, Name: 'KOMPAS TV' }
                    ]
                },
                {
                    Description: 'Second Set',
                    Channels: [
                        { Number: 373, Name: 'DISNEY HD' },
                        { Number: 328, Name: 'FOX HD' }
                    ]
                }
            ],
            SelectedSet: 0
        }
        return settings;
    },
    resetSettings() {
        let s = this.getDefaultSettings();
        let ss = JSON.stringify(s);
        localStorage.setItem(LOCALSTORAGEKEY, ss);
        return s;
    },
    readFromLocalStorage() {
        let settings = localStorage.getItem(LOCALSTORAGEKEY);
        if (!settings) {
            settings = this.resetSettings();
        } else {
            settings = JSON.parse(settings);
            if (!this.isSettingsValid(settings)) {
                settings = this.resetSettings();
            }
        }
        return settings;
    },
    readFromEvent(event) {
        if (event.key != "settings") {
            return null;
        }
        let settings = JSON.parse(event.newValue);
        if (!this.isSettingsValid(settings)) {
            settings = this.resetSettings();
        }
        return settings;
    },
    writeToLocalStorage(settings) {
        if (!this.isSettingsValid(settings)) {
            throw "Invalid Settings"
        }
        let settingsString = JSON.stringify(settings);
        localStorage.setItem(LOCALSTORAGEKEY, settingsString);
    }
}

export default settingsHelper;
